"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import DashboardTitle from "@/components/dashboard/DashboardTitle";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { toast } from "react-hot-toast";
import { useCredentials } from "@/hooks/useCredentials";
import { Skeleton } from "@/components/ui/skeleton";

interface KMSKey {
  KeyId: string;
  Arn: string;
  AliasName?: string;
  Description?: string;
  CreationDate?: string;
  Enabled?: boolean;
  KeyState?: string;
  KeyUsage?: string;
  Origin?: string;
  KeyManager?: string;
}

export default function KMSPage() {
  const { credentials, loading: credentialsLoading } = useCredentials();
  const [selectedCredential, setSelectedCredential] = useState<string>("");
  const [keys, setKeys] = useState<KMSKey[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (credentials && credentials.length > 0 && !selectedCredential) {
      setSelectedCredential(credentials[0].id);
    }
  }, [credentials, selectedCredential]);

  useEffect(() => {
    if (selectedCredential) {
      fetchKeys();
    }
  }, [selectedCredential]);

  const fetchKeys = async () => {
    if (!selectedCredential) return;

    setLoading(true);
    setError("");

    try {
      const response = await fetch(`/api/aws/kms?credentialId=${selectedCredential}`);
      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Falha ao obter chaves KMS");
      }

      setKeys(data.keys || []);
    } catch (err: any) {
      console.error("Erro ao buscar chaves KMS:", err);
      setError(err.message || "Ocorreu um erro ao buscar as chaves KMS");
      toast.error("Erro ao carregar chaves KMS");
    } finally {
      setLoading(false);
    }
  };

  const getKeyStateColor = (state?: string) => {
    if (!state) return "bg-gray-200 text-gray-800";
    
    switch (state) {
      case "Enabled":
        return "bg-green-100 text-green-800";
      case "Disabled":
        return "bg-yellow-100 text-yellow-800";
      case "PendingDeletion":
        return "bg-red-100 text-red-800";
      case "PendingImport":
        return "bg-blue-100 text-blue-800";
      default:
        return "bg-gray-200 text-gray-800";
    }
  };

  const formatDate = (dateString?: string) => {
    if (!dateString) return "N/A";
    return new Date(dateString).toLocaleDateString("pt-BR", {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit"
    });
  };

  return (
    <div className="container mx-auto px-4 py-6">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <DashboardTitle
          title="Gerenciamento de Chaves KMS"
          description="Visualize e gerencie suas chaves de criptografia AWS KMS (Key Management Service)"
        />

        <div className="my-6">
          <Card className="p-6">
            <div className="flex flex-col md:flex-row justify-between mb-6 gap-4">
              <div className="flex flex-col md:flex-row gap-4 items-center">
                <select
                  value={selectedCredential}
                  onChange={(e) => setSelectedCredential(e.target.value)}
                  disabled={credentialsLoading}
                  className="px-3 py-2 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  {credentialsLoading ? (
                    <option>Carregando credenciais...</option>
                  ) : (
                    credentials.map((cred) => (
                      <option key={cred.id} value={cred.id}>
                        {cred.name} ({cred.accountId})
                      </option>
                    ))
                  )}
                </select>

                <Button
                  onClick={fetchKeys}
                  disabled={loading || !selectedCredential}
                  className="bg-indigo-600 hover:bg-indigo-700 text-white"
                >
                  {loading ? "Carregando..." : "Atualizar"}
                </Button>
              </div>

              <Button
                onClick={() => {
                  // Implementar criação de chave
                  toast.error("Funcionalidade de criar chave ainda não implementada");
                }}
                className="bg-green-600 hover:bg-green-700 text-white"
              >
                Criar Chave
              </Button>
            </div>

            {error && (
              <div className="bg-red-100 text-red-800 p-4 mb-6 rounded-md">
                {error}
              </div>
            )}

            {loading ? (
              <div className="space-y-4">
                {[1, 2, 3].map((i) => (
                  <div key={i} className="flex flex-col gap-2">
                    <Skeleton className="h-8 w-full" />
                    <Skeleton className="h-16 w-full" />
                  </div>
                ))}
              </div>
            ) : keys.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                Nenhuma chave KMS encontrada. Crie uma nova chave ou selecione outra credencial.
              </div>
            ) : (
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        ID / Alias
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Descrição
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Criado em
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Estado
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Tipo
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Origem
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Ações
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {keys.map((key) => (
                      <tr key={key.KeyId} className="hover:bg-gray-50">
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="flex items-center">
                            <div className="ml-4">
                              <div className="text-sm font-medium text-gray-900">
                                {key.AliasName || "Sem alias"}
                              </div>
                              <div className="text-xs text-gray-500 truncate max-w-xs">
                                {key.KeyId}
                              </div>
                            </div>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="text-sm text-gray-900">
                            {key.Description || "Sem descrição"}
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {formatDate(key.CreationDate)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${getKeyStateColor(key.KeyState)}`}>
                            {key.KeyState || "Desconhecido"}
                          </span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {key.KeyUsage || "N/A"}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {key.Origin || "AWS_KMS"}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                          <div className="flex space-x-2">
                            <button
                              onClick={() => {
                                // Implementar ação de editar
                                toast.error("Funcionalidade não implementada");
                              }}
                              className="text-indigo-600 hover:text-indigo-900"
                            >
                              Editar
                            </button>
                            <button
                              onClick={() => {
                                // Implementar ação de deletar
                                toast.error("Funcionalidade não implementada");
                              }}
                              className="text-red-600 hover:text-red-900"
                            >
                              Deletar
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </Card>
        </div>

        <div className="mt-8">
          <Card className="p-6">
            <h3 className="text-lg font-medium mb-4">Informações Importantes</h3>
            <div className="space-y-4 text-sm text-gray-600">
              <p>
                <strong>AWS KMS (Key Management Service)</strong> permite criar e gerenciar chaves criptográficas e controlar seu uso em diversos serviços AWS.
              </p>
              <ul className="list-disc pl-5 space-y-2">
                <li><strong>Aliases:</strong> Nomes amigáveis para identificar suas chaves.</li>
                <li><strong>Estado:</strong> Determina se a chave está ativa, desativada ou pendente de exclusão.</li>
                <li><strong>Origem:</strong> Indica onde o material da chave foi criado (AWS KMS, importado externamente ou AWS CloudHSM).</li>
                <li><strong>Tipo de uso:</strong> Define para quais operações a chave pode ser utilizada (criptografia/descriptografia, assinatura/verificação, etc.).</li>
              </ul>
              <p className="text-yellow-600">
                <strong>Atenção:</strong> A exclusão de chaves é irreversível. Use com cuidado, pois dados criptografados com uma chave excluída não poderão ser recuperados.
              </p>
            </div>
          </Card>
        </div>
      </motion.div>
    </div>
  );
} 